<template>
  <Home v-if="this.mode === 'Home'" id="Home" @MoveLadder='MoveLadder' @MoveCPS='MoveCPS' @MoveReport='MoveReport' />
  <Report v-else-if="this.mode === 'Report'" id="Report" @X='X' />
  <Ladder v-else-if="this.mode === 'Ladder'" id="Ladder" @X='X' />
  <CPS v-else-if="this.mode === 'CPS'" id="CPS" @X='X' />
</template>

<script>
import Home from './components/Home.vue'
import Report from './components/report.vue'
import Ladder from './components/games/ladder.vue'
import CPS from './components/games/cps.vue'

export default {
  name: 'App',
  components: {
    Home,
    Report,
    Ladder,
    CPS
  },
  data() {
    return {
      mode: '',
    }
  },
  methods: {
    // Move to Ladder
    MoveLadder: function() {
      this.mode = 'Ladder';
    },
    // Move to CPS
    MoveCPS: function() {
      this.mode = 'CPS';
    },
    // Move to Report page
    MoveReport: function() {
      this.mode = 'Report';
    },
    // X
    X() {
        this.mode = 'Home'; 
        location.reload();
    }
  },
  created() {
    this.mode = 'Home';
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  
}


</style>
