<template>
    <div class="ladder">
        <div id="X" @click="X">
            <svg @click="X" id='X-icon' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" style="fill: rgba(0, 0, 0, 1);transform: ;msFilter:;"><path d="m16.192 6.344-4.243 4.242-4.242-4.242-1.414 1.414L10.535 12l-4.242 4.242 1.414 1.414 4.242-4.242 4.243 4.242 1.414-1.414L13.364 12l4.242-4.242z"></path></svg>
        </div>
        <!-- game view -->
        <div id="block">
            <div id="block1" class="blocks block1" @click="GF($event)" ></div>
            <div id="block2" class="blocks block2" @click="GF($event)" ></div>
            <div id="block3" class="blocks block3" @click="GF($event)" ></div>
            <div id="block4" class="blocks block4" @click="GF($event)" ></div>
            <div id="block5" class="blocks block5" @click="GF($event)" ></div>
            <div id="block6" class="blocks block6" @click="GF($event)" ></div>
            <div id="block7" class="blocks block7" @click="GF($event)" ></div>
