            <div id="block9" class="blocks block9" @click="GF($event)" ></div>
        </div>
        <!--  -->

        <!-- if true -->
        <h1 id="end" >YOU LOSE!</h1>
        <!--  -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            rand: null
        }
    },
    methods: {
        // X
        X() {
            this.$emit('X')
        },
        // game function
        GF(event) {
            if (event.target.id == this.rand) {
                // if true
                document.querySelector('#block').style.display = 'none';
                document.querySelector('#end').style.display = 'block';

            }else{
                // if else
                document.querySelector('#'+event.target.id).style.display = 'none';
            }
        }
    },
    created() {
        // RANDOM NUMBER max9
        this.rand = 'block' + Math.floor(Math.random() * 10);

        if (this.rand == 'block0') {
            this.rand = 'block' + Math.floor(Math.random() * 10);
        }
    }
}
</script>

